@mixin font-face($font-family, $file-path, $font-weight, $font-style) {
  @font-face {
    font-family: $font-family;
      src: url('#{$file-path}.eot');
      src: url('#{$file-path}.eot?#iefix') format('embedded-opentype'),
           url('#{$file-path}.woff') format('woff'),
           url('#{$file-path}.ttf') format('truetype');
    font-weight: $font-weight;
    font-style: $font-style;
  }
}

@include font-face(Lato, lato-regular-webfont, normal, normal);
@include font-face(Lato, lato-italic-webfont, normal, italic);
@include font-face(Lato, lato-bold-webfont, bold, normal);
@include font-face(Lato, lato-bolditalic-webfont, bold, italic);
